.container {
  margin: auto;
  position: relative;
  display: table;
  min-height: 100px;
  max-width: 85%;
  vertical-align: middle;
}
.container > div {
  margin-bottom: 2rem;
}
.container-full {
  position: relative;
  display: table;
  min-height: 100px;
  width: 100%;
}
@media screen and (max-width: 100vw) {
  .col {
    display: table-cell;
    width: calc(100% / 2);
  }
  .col-small {
    display: table-cell;
    width: calc(100% / 3);
  }
  .container {
    width: 100%;
  }
}
.card {
  margin: auto auto;
  min-width: 350px;
  max-width: 960px;
  background: white;
  border-radius: 6px;
  overflow: hidden;
}
.card .card-wrapper {
  margin-bottom: 4rem;
}
.card .card-image-wrapper {
  position: relative;
  height: 175px;
  overflow: hidden;
  max-width: 100%;
}
.card .card-img {
  max-width: 100%;
  height: 100%;
  background-size: cover;
}
.card .card-img-title {
  position: absolute;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.7);
  width: 100%;
  color: black;
  padding: 0.3em 2em 0.3em 1rem;
  margin: 2rem;
}
.card .card-text-content {
  margin: 2rem;
  padding: 1em 0.8em;
}
.card .card-text-footer {
  padding: 0.5em;
  display: flex;
  justify-content: space-between;
}
.card .card-text-link {
  color: black;
  text-decoration: none;
  display: inline-block;
  padding: 0.5em 0.4em 0.5em;
  transition: 0.5s;
}
.card .card-text-link:hover {
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
}
.card .card-small {
  position: relative;
  max-width: 100%;
  background: white;
  border-radius: 6px;
  overflow: hidden;
}
.card .card-small-header {
  position: relative;
  max-width: 100%;
  height: 75px;
}
.card .card-small-title {
  font-size: 1.2em;
  position: absolute;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.25);
  padding: 0.3em 1em;
  text-align: center;
  width: 100%;
  color: white;
}
.card .card-small-img {
  height: 100%;
  width: 100%;
}
.card .card-small-img-item {
  position: absolute;
  height: 30px;
  width: 30px;
  top: 50%;
  left: 50%;
  border-radius: 20px;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.87);
}
.card .card-small-content {
  padding: 1em 0.8em;
}
.shadow-md {
  box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.5);
}
.card-text-divider {
  width: 100%;
  margin: 0.5rem;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.23);
}
.col-half {
  float: left;
  padding: 1rem;
  width: calc(100% / 2);
}
.col {
  float: left;
  padding: 1rem;
  width: calc(100% / 3.5);
}
.col-small {
  float: left;
  width: calc(100% / 6);
}
@media screen and (max-width: 690px) {
  .col {
    width: 100%;
  }
  .col-small {
    width: calc(100% / 2);
  }
}
.row {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  max-width: 950px;
}
/* Color Theme Swatches in Hex */
header {
  width: 100%;
  background: #8F9DA6;
  z-index: 9999;
  top: 0;
  position: fixed;
}
.navigation-bar {
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, Courier, monospace;
  background-color: #8F9DA6;
  border: 1px solid #5c6a73;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1000;
}
.navigation-bar a {
  color: white;
  text-decoration: none;
}
.navigation-bar ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.navigation-bar ul li {
  display: inline-block;
  padding: 1em;
}
.navigation-bar h1 {
  color: white;
  margin: 0 !important;
  font-size: 1.5rem;
  font-weight: bold;
}
.navigation-bar .item {
  color: white;
  border-bottom: #BF6A4B 2px solid;
}
.navigation-bar .item button {
  border: 0;
  background: inherit;
  font-size: inherit;
}
.item:not(:hover) a {
  transition: color 0.5s;
}
.item:hover button {
  color: #BF6A4B;
  transition: color 0.5s;
}
.navigation-bar:not(:hover) h1 {
  color: white;
  opacity: 1;
  transition: opacity 0.25s, color 0.5s;
}
.navigation-bar:hover h1 {
  opacity: 0.8;
  transition: opacity 0.25s, color 0.5s;
}
.content {
  min-height: 1200px;
}
.block {
  margin: auto auto;
  min-width: 350px;
  max-width: 960px;
  background: white;
  border-radius: 6px;
  overflow: hidden;
}
